.career_resources_banner .banner{
    min-height: 100%;
}

.banner{
    background-size: cover;
    position: relative;
    z-index: 0;

}
.career_resources_banner .banner img .d-md-block{
height: 640px;
}
.container-lg{
    max-width: 1260px;
}
.about_banner.career_resources_banner .banner_content{
    position: absolute;
    top:50% ;
    left:0 ;
    right: 0;
    transform:translateY(-50%) ;
    text-align:center ;

}
.career_resources_banner.about_banner .banner_content h1{
    width:100% ;
    max-width:900px ;
    margin:0 auto ;
    line-height:1.2 ;
    font-weight: 700;
    padding:0 16px ;
    color: #fff;
}
